body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  text-align: center;
}
.clock {
  width: 300px;
  margin: 20px auto;
}
.circle {
  border: 1px solid red;
  height: 300px;
  border-radius: 50%;
}
.tick {
  height: 6px;
  width: 2px;
  background: black;
  position: absolute;
  margin-left: 150px;
  transform-origin: 0 150px;
}
.tick:nth-of-type(5n) {
  width: 4px;
  height: 12px;
}

.tick:nth-of-type(15n) {
  width: 4px;
  height: 20px;
}
.hands div {
  position: absolute;
  border-top-left-radius: 70%;
  border-top-right-radius: 70%;
  z-index: 10;
}
.sec {
  width: 2px;
  height: 260px;
  margin-left: 147px;
  margin-top: -280px;
  background-image: linear-gradient(red 60%, transparent 40%);
}
.min {
  width: 4px;
  height: 240px;
  margin-left: 148px;
  margin-top: -270px;
  background-image: linear-gradient(black 50%, transparent 50%);
}
.hour {
  background-image: linear-gradient(black 50%, transparent 50%);
  margin-left: 144px;
  margin-top: -230px;
  width: 6px;
  height: 160px;
}
.center {
  margin-left: 135px;
  margin-top: -165px;
  z-index: 999;
  background: #999;
  height: 30px;
  width: 30px;
  position: absolute;
  border-radius: 50%;
}
